.userInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 10px;
    color: goldenrod;
}

.userInfo:hover .navLink{
    opacity: 1;
}

.userInfo .navLink{
    opacity: 0;
    transition: opacity 0.3s;
}

.avatar{
    height: 40px;
    width: 40px;
}